import React from "react";

const Select = ({ label, options, error, ...rest }) => {
  return (
    <React.Fragment>
      <select className={error ? "select_error select" : "select"} {...rest}>
        <option
          defaultValue={label}
          value={label}
          hidden
          style={{ display: "none" }}
        >
          Gender
        </option>
        {options.map((option) => (
          <option key={option} value={option}>
            {option}
          </option>
        ))}
      </select>
      <div className="error_msg">{error}</div>
    </React.Fragment>
  );
};

export default Select;
