import React from "react";
import { emailVerify } from "../services/emailService";
import { resetPassword } from "../services/resetService";
import { toast } from "react-toastify";
import Form from "./common/form";
import Joi from "joi";

class ResetPassword extends Form {
  state = {
    sucess: false,
    userId: this.props.match.params.userId,
    token: this.props.match.params.token,
    data: { password: "", confirmPassword: "" },
    errors: {},
    fetch: false,
  };

  schema = {
    password: Joi.string().min(6).max(1000).required(),
    confirmPassword: Joi.string().min(6).max(1000).required().label("password"),
  };

  async componentDidMount() {
    const { userId, token } = this.state;
    try {
      await emailVerify(userId, token);
      this.setState({ sucess: true });
    } catch (error) {
      this.props.history.push("/not-found");
    }
  }

  doSubmit = async () => {
    const { userId, token, data } = this.state;
    if (data.password !== data.confirmPassword) {
      const errors = { ...this.state.errors };
      errors.password = "password does not match";
      errors.confirmPassword = "password does not match";
      this.setState({ errors });
      return;
    }
    try {
      this.setState({ fetch: true });
      await resetPassword(userId, token, data.password);
      toast.success("password reset sucessfully");
      this.setState({ fetch: false });
      this.props.history.push("/");
    } catch (error) {
      if (error.response && error.response.status === 400) {
        toast.error(error.response.data);
        this.setState({ fetch: false });
      }
    }
  };

  render() {
    const { sucess } = this.state;
    return (
      <React.Fragment>
        {sucess && (
          <div className="flex container">
            <form onSubmit={this.handleSubmit} className="form_container">
              <div className="form_heading">Reset Password</div>
              {this.renderInput("password", "password", "Password", true)}
              {this.renderInput(
                "confirmPassword",
                "password",
                "Confirm Password",
                true
              )}
              {this.renderSubmitBtn("Submit")}
            </form>
          </div>
        )}
      </React.Fragment>
    );
  }
}

export default ResetPassword;
