import React from "react";
import { Link } from "react-router-dom";
import { getUserDetails, updateUserDetails } from "../services/userService";
import { toast } from "react-toastify";
import Joi from "joi";
import Form from "./common/form";
import ProfileStyle from "./common/profileStyle";
import checked from "../assets/checked.svg";

class Profile extends Form {
  state = {
    data: {
      username: "",
      email: "",
      mobile: "",
      gender: "",
      dateOfBirth: "",
    },
    errors: {},
    edit: false,
    fetch: false,
  };

  schema = {
    username: Joi.string().min(3).max(250).required(),
    email: Joi.string().email({ tlds: false }).required(),
    mobile: Joi.string().min(10).max(10).allow(""),
    gender: Joi.string().required(),
    dateOfBirth: Joi.date().allow(""),
  };

  async componentDidMount() {
    try {
      const { data } = await getUserDetails();
      this.setState({ data });
    } catch (error) {
      console.log(error);
    }
  }

  handleEdit = () => {
    this.setState({ edit: true });
  };

  doSubmit = async () => {
    const { data } = this.state;
    try {
      this.setState({ fetch: true });
      await updateUserDetails(data);
      this.setState({ fetch: false });
      window.location.reload();
    } catch (error) {
      if (error.response && error.response.status === 400) {
        toast.error(error.response.data);
        this.setState({ fetch: false });
      }
    }
  };

  render() {
    const { data, edit } = this.state;
    return (
      <div className="profile_wraper">
        <ProfileStyle />
        <div className="profile_container">
          <div className="profile_data">
            {!edit && (
              <div className="profile_details">
                {["username", "email", "mobile", "gender", "dateOfBirth"].map(
                  (name) => (
                    <div className="profile_option" key={name}>
                      <div className="profile_label">
                        {name === "dateOfBirth" ? "birth date" : name}
                      </div>
                      <div style={{ padding: "10px" }}>:</div>
                      <div className="profile_value">
                        {data[name] ? data[name] : "not added"}
                      </div>
                      {name === "email" && (
                        <img
                          alt="verified"
                          src={checked}
                          style={{
                            width: "15px",
                            height: "15px",
                            margin: "0 15px",
                          }}
                        />
                      )}
                    </div>
                  )
                )}
                <div style={{ marginTop: "20px" }} className="profile_option">
                  <Link to="/forgot-password">
                    <button style={{ marginRight: "10px" }} className="btn">
                      Change password
                    </button>
                  </Link>
                  <button onClick={this.handleEdit} className="btn">
                    edit
                  </button>
                </div>
              </div>
            )}
            {edit && (
              <form onSubmit={this.handleSubmit} className="flex column">
                {this.renderInput("username", "text", "Username", true)}
                {this.renderInput("email", "email", "Email", true, true)}
                {this.renderInput("mobile", "text", "Mobile")}
                {this.renderSelect("gender", "Gender", ["male", "female"])}
                {this.renderInput("dateOfBirth", "date", "Date Of Birth")}
                {this.renderSubmitBtn("Save", "262px")}
              </form>
            )}
          </div>
        </div>
      </div>
    );
  }
}

export default Profile;
