import { resendEmail } from "../services/emailService";
import { toast } from "react-toastify";
import checked from "../assets/checked.svg";

const EmailResend = ({ match, location }) => {
  const email = match.params.email;
  const account = `/account/email-resend/${email}`;
  const password = `/password-reset/email-resend/${email}`;

  const resend = async () => {
    if (account === location.pathname) {
      try {
        await resendEmail(email, "verifyEmail");
        toast.success("Email sent sucessfully");
        return;
      } catch (error) {
        if (error.response && error.response.status === 400) {
          toast.error(error.response.data);
        }
      }
    }
    if (password === location.pathname) {
      try {
        await resendEmail(email, "resetEmail");
        toast.success("Email sent sucessfully");
        return;
      } catch (error) {
        console.log(error.response.data);
        toast.error(error.response.data);
      }
    }
  };

  return (
    <div className="flex container">
      <div className="email_container flex column">
        <img
          src={checked}
          alt="checked"
          style={{ width: "100px", height: "100px" }}
        />
        <h5 className="text_center">
          A link has been sent to your email account
        </h5>
        <h6 className="text_center" style={{ margin: "0" }}>
          Please click on the link that has just been sent to your email account
          : {email}.
        </h6>
        <button onClick={resend} className="resendBtn">
          resend link
        </button>
      </div>
    </div>
  );
};

export default EmailResend;
