import React from "react";
import { resendEmail } from "../services/emailService";
import Joi from "joi";
import Form from "./common/form";

class ForgotPassword extends Form {
  state = { data: { email: "" }, errors: {}, fetch: false };

  schema = { email: Joi.string().email({ tlds: false }).required() };

  doSubmit = async () => {
    const { data } = this.state;
    try {
      this.setState({ fetch: true });
      await resendEmail(data.email, "resetEmail");
      this.props.history.push(`/password-reset/email-resend/${data.email}`);
      this.setState({ fetch: false });
    } catch (error) {
      if (error.response && error.response.status === 400) {
        const errors = { ...this.state.errors };
        errors.email = error.response.data;
        this.setState({ errors, fetch: false });
      }
    }
  };

  render() {
    return (
      <div className="flex container">
        <form onSubmit={this.handleSubmit} className="form_container">
          <div className="form_heading">Forgot Password?</div>
          <p
            className="text_center"
            style={{ width: "250px", fontSize: "10px" }}
          >
            Enter the email address associated with your account and we will
            send you a link to reset your password.
          </p>
          {this.renderInput("email", "email", "Email", true)}
          {this.renderSubmitBtn("submit")}
        </form>
      </div>
    );
  }
}

export default ForgotPassword;
