import React, { Component } from "react";
import { emailVerify } from "../services/emailService";
import checked from "../assets/checked.svg";

class EmailVerify extends Component {
  state = { sucess: false };

  async componentDidMount() {
    const userId = this.props.match.params.userId;
    const token = this.props.match.params.token;
    try {
      await emailVerify(userId, token);
      this.setState({ sucess: true });
    } catch (error) {
      this.props.history.push("/not-found");
    }
  }

  render() {
    const { sucess } = this.state;
    return (
      <React.Fragment>
        {sucess && (
          <div className="flex container">
            <div className="email_container flex column">
              <img
                src={checked}
                alt="checked"
                style={{ width: "100px", height: "100px" }}
              />
              <h2 className="text_center">Thank You</h2>
              <h5 className="text_center" style={{ margin: "0" }}>
                You have verified your email.
              </h5>
              <button
                onClick={() => this.props.history.push("/")}
                className="resendBtn"
              >
                Continue
              </button>
            </div>
          </div>
        )}
      </React.Fragment>
    );
  }
}

export default EmailVerify;
