import React from "react";
import { Link } from "react-router-dom";
import Joi from "joi";
import { register } from "../services/userService";
import { toast } from "react-toastify";
import registerImg from "../assets/register.svg";
import Form from "./common/form";

class Register extends Form {
  state = {
    data: {
      username: "",
      email: "",
      gender: "",
      password: "",
      confirmPassword: "",
    },
    fetch: false,
    errors: {},
  };

  schema = {
    username: Joi.string().min(3).max(250).required(),
    email: Joi.string().email({ tlds: false }).required(),
    gender: Joi.string().required(),
    password: Joi.string().min(6).max(1000).required(),
    confirmPassword: Joi.string().min(6).max(1000).required().label("password"),
  };

  doSubmit = async () => {
    const { data } = this.state;
    if (data.password !== data.confirmPassword) {
      const errors = { ...this.state.errors };
      errors.password = "password does not match";
      errors.confirmPassword = "password does not match";
      this.setState({ errors });
      return;
    }
    try {
      this.setState({ fetch: true });
      await register(this.state.data);
      this.props.history.push(`/account/email-resend/${data.email}`);
      this.setState({ fetch: false });
    } catch (error) {
      if (error.response && error.response.status === 400) {
        toast.error(error.response.data);
        this.setState({ fetch: false });
      }
    }
  };

  render() {
    return (
      <div className="flex space_around container">
        <form onSubmit={this.handleSubmit} className="form_container">
          <div className="form_heading">Register</div>
          {this.renderInput("username", "text", "Username", true)}
          {this.renderInput("email", "email", "Email", true)}
          {this.renderSelect("gender", "Gender", ["male", "female"])}
          {this.renderInput("password", "password", "Password", true)}
          {this.renderInput(
            "confirmPassword",
            "password",
            "Confirm Password",
            true
          )}
          {this.renderSubmitBtn("Register")}
          <div
            style={{ textAlign: "center", fontSize: "14px", marginTop: "10px" }}
          >
            Already have an account? {"  "}
            <Link to="/login">Login</Link>
          </div>
        </form>
        <img className="form_image" alt="register" src={registerImg} />
      </div>
    );
  }
}

export default Register;
