import React from "react";
import { Link } from "react-router-dom";
import { login } from "../services/authService";
import Joi from "joi";
import loginImg from "../assets/login.svg";
import Form from "./common/form";

class Login extends Form {
  state = { data: { email: "", password: "" }, errors: {}, fetch: false };

  schema = {
    email: Joi.string().email({ tlds: false }).required(),
    password: Joi.string().min(6).max(1000).required(),
  };

  doSubmit = async () => {
    try {
      this.setState({ fetch: true });
      const { data } = await login(this.state.data);

      if (data === "verify_email") {
        return this.props.history.push(
          `/account/email-resend/${this.state.data.email}`
        );
      }

      localStorage.setItem("token", data);
      this.setState({ fetch: false });
      this.props.history.push("/");
    } catch (error) {
      if (error.response && error.response.status === 400) {
        const errors = { ...this.state.errors };
        errors.email = error.response.data;
        errors.password = error.response.data;
        this.setState({ errors, fetch: false });
      }
    }
  };

  render() {
    return (
      <div className="flex space_around container">
        <form onSubmit={this.handleSubmit} className="form_container">
          <div className="form_heading">Login</div>
          {this.renderInput("email", "email", "Email", true)}
          {this.renderInput("password", "password", "Password", true)}
          <div style={{ textAlign: "end", fontSize: "14px" }}>
            <Link to="/forgot-password">
              <p>Forgot password?</p>
            </Link>
          </div>
          {this.renderSubmitBtn("Login")}
        </form>
        <img className="form_image" alt="loginImg" src={loginImg} />
      </div>
    );
  }
}

export default Login;
