import { NavLink } from "react-router-dom";

const ProfileStyle = () => {
  return (
    <div className="profile_options">
      <NavLink
        to="/me"
        activeClassName="active"
        className="remove_a profile_btn"
      >
        profile
      </NavLink>
      <NavLink
        to="/addresses"
        activeClassName="active"
        className="remove_a profile_btn flex"
      >
        addresses
      </NavLink>
      <NavLink
        to="/orders"
        activeClassName="active"
        className="remove_a profile_btn flex"
      >
        orders
      </NavLink>
    </div>
  );
};

export default ProfileStyle;
