import React, { Component } from "react";
import Joi from "joi";
import Input from "./input";
import Select from "./select";

class Form extends Component {
  state = { data: {}, errors: {} };

  validate = () => {
    const schema = Joi.object(this.schema);
    const { error } = schema.validate(this.state.data);
    if (!error) return null;

    const errors = {};
    console.log(error);
    for (let item of error.details) errors[item.path[0]] = item.message;
    return errors;
  };

  validateProperty = ({ name, value }) => {
    const obj = { [name]: value };
    const schema = Joi.object({ [name]: this.schema[name] });
    const { error } = schema.validate(obj);
    return error ? error.details[0].message : null;
  };

  handleChange = ({ currentTarget: input }) => {
    const errors = { ...this.state.errors };
    const errorMessage = this.validateProperty(input);
    if (errorMessage) errors[input.name] = errorMessage;
    else delete errors[input.name];

    const data = { ...this.state.data };
    data[input.name] = input.value;

    this.setState({ data, errors });
  };

  handleSubmit = (e) => {
    e.preventDefault();

    const errors = this.validate();
    this.setState({ errors: errors || {} });
    if (errors) return;

    this.doSubmit();
  };

  renderInput(name, type, placeholder, required, disabled = false) {
    const { data, errors } = this.state;
    return (
      <Input
        value={data[name]}
        name={name}
        type={type}
        placeholder={placeholder}
        onChange={this.handleChange}
        required={required}
        error={errors[name]}
        disabled={disabled}
      />
    );
  }

  renderSelect(name, label, options, required = true) {
    const { data, errors } = this.state;
    return (
      <Select
        label={label}
        value={data[name]}
        name={name}
        onChange={this.handleChange}
        options={options}
        error={errors[name]}
        required={required}
      />
    );
  }

  renderSubmitBtn(name, width = "100%") {
    const { fetch } = this.state;
    return (
      <button
        type="submit"
        style={{ width: `${width}`, margin: "5px 0" }}
        className="btn flex"
      >
        {fetch && <div className="loader"></div>}
        {!fetch && name}
      </button>
    );
  }
}

export default Form;
