import http from "./httpService";
const apiUrl = process.env.REACT_APP_API_URL;

const emailResendApiEndpoint = apiUrl + "/verify/resend-email";

export function emailVerify(userId, token) {
  return http.get(`${apiUrl}/verify/account/${userId}/${token}`);
}

export function resendEmail(email, emailType) {
  return http.post(emailResendApiEndpoint, {
    email: email,
    emailType: emailType,
  });
}
