import React, { Component } from "react";
import { Route, Switch } from "react-router-dom";
import { ToastContainer } from "react-toastify";
import RedirectRegisterUser from "./utils/redirectRegisterUser";
import RoutesWithNav from "./utils/routesWithNav";
import Login from "./components/login";
import Register from "./components/register";
import Logout from "./components/logout";
import NotFound from "./components/notFound";
import EmailResend from "./components/emailResend";
import EmailVerify from "./components/emailVerify";
import ForgotPassword from "./components/forgotPassword";
import ResetPassword from "./components/resetPassword";
import "react-toastify/dist/ReactToastify.css";
import "./App.css";
import "./main.css";
import "./form.css";

class App extends Component {
  state = {};

  render() {
    return (
      <React.Fragment>
        <ToastContainer />
        <Switch>
          <Route exact path="/logout" component={Logout}></Route>
          <Route
            exact
            path="/forgot-password"
            component={ForgotPassword}
          ></Route>
          <RedirectRegisterUser
            exact
            path="/register"
            component={Register}
          ></RedirectRegisterUser>
          <RedirectRegisterUser
            exact
            path="/login"
            component={Login}
          ></RedirectRegisterUser>
          <Route
            exact
            path="/verify/account/:userId/:token"
            component={EmailVerify}
          ></Route>
          <Route
            exact
            path="/password-reset/email-resend/:email"
            component={EmailResend}
          ></Route>
          <Route
            exact
            path="/account/email-resend/:email"
            component={EmailResend}
          ></Route>
          <Route
            exact
            path="/password-reset/:userId/:token"
            component={ResetPassword}
          ></Route>
          <Route path="/not-found" component={NotFound}></Route>
          <RoutesWithNav />
        </Switch>
      </React.Fragment>
    );
  }
}

export default App;
