import React, { Component } from "react";
import { Link } from "react-router-dom";
import { getCurrentUser } from "../../services/authService";
import profile from "../../assets/user.svg";

class NavBar extends Component {
  state = {};

  componentDidMount() {
    const user = getCurrentUser();
    this.setState({ user });
  }
  render() {
    const { user } = this.state;
    return (
      <div className="flex navbar">
        <Link to="/" className="remove_a">
          <div className="logo">Vibind</div>
        </Link>
        {!user && (
          <div className="nav_btns">
            <Link to="/register">
              <button className="nav_icons btn">register</button>
            </Link>
            <Link to="/login">
              <button className="nav_icons btn">login</button>
            </Link>
          </div>
        )}
        {user && (
          <div className="nav_btns flex">
            <Link to="/me">
              <img src={profile} alt="profile" className="profile_img" />
            </Link>
            <Link to="/logout">
              <button className="nav_icons btn">logout</button>
            </Link>
          </div>
        )}
      </div>
    );
  }
}

export default NavBar;
