const Input = ({ error, ...rest }) => {
  return (
    <div className="flex column">
      {error ? (
        <input
          {...rest}
          style={{ borderColor: "red" }}
          className="input"
        ></input>
      ) : (
        <input {...rest} className="input"></input>
      )}
      <div className="error_msg">{error}</div>
    </div>
  );
};

export default Input;
